.header {
  background-color: #1E2228;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.header .container {
  padding-top: 15px;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-between;

  @include vp-767 {
    padding-bottom: 13px;
  }
}

.header__logo-wrapper {
  display: flex;
  align-items: center;

  span {
    @include font-settings ($ff-inter, 600, 14px, 1);
    padding-right: 9px;
  }
}

.header__logo {
  width: 190px;
  height: 42px;
  padding-top: 5px;
  margin-right: 9px;

  @include vp-767 {
    width: 127px;
    height: 20px;
    margin-left: 15px;
    padding: 0;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: contain;

    &:hover {
      opacity: 0.6;
    }
  }
}

.header__partner-logo {
  max-width: 150px;
  height: 30px;
  display: flex;
  align-items: center;

  @include vp-767 {
    max-width: 60px;
    height: 17px;
  }

  img {
    width: 100%;
    object-fit: contain;

    &:hover {
      opacity: 0.6;
    }
  }
}

.header__search {
  @include vp-1023 {
    display: none;
  }
}
